.inputForm {
	margin-top: 25px;
	margin-bottom: 25px;
	display: flex;
	justify-content: center;
}

.inputForm>input {
	padding: 15px;
	width: 250px;
	border-radius: 50px;
/* border: none; */
	/* padding: 20px;
	width: 300px;
	color: white;
	background-color: rgba(0, 0, 0, 0.633); */
	text-align: center;
	border: 2px solid #6b26d9;
	font-size: 25px;
	backdrop-filter: blur(10px);
outline: none;
	font-family: inherit;


}

/* ..inputForm>input::placeholder {
	color: white;
} */
.inputForm>input:focus {
	background-color: rgba(127, 29, 233, 0.071);
	border: none;
	border: 2px solid #6b26d9 ;

	/* color: white; */
}

.inputForm>button {
	padding: 5px;
	width: 70px;
	height: 70px;
	border-radius: 50px;
	border: 2px solid #6b26d9;
	outline: none;
	padding: 15px;
	text-align: center;
	/* border: 2px solid black; */
	font-size: 25px;
	backdrop-filter: blur(10px);
	font-family: inherit;

}

